import { NextRequest, NextResponse } from "next/server";
import { Server as SocketIOServer } from "socket.io";
import { createServer } from "http";

// In-memory Pomodoro state (in production, use a database)
let pomodoroState = {
  timeLeft: 1500, // 25 minutes in seconds
  isActive: false,
  mode: "work" as "work" | "shortBreak" | "longBreak",
  session: 1,
  initiatedBy: null as string | null,
  nextBreakType: "short" as "short" | "long",
  startTime: null as number | null,
  pausedAt: null as number | null,
  totalPausedTime: 0,
};

// Timer interval
let pomodoroInterval: NodeJS.Timeout | null = null;

// Socket.io instance (if needed for real-time updates)
let io: SocketIOServer | null = null;

// GET - Get current Pomodoro status with real-time calculation
export async function GET() {
  try {
    // If timer is active, calculate current time left based on elapsed time
    if (pomodoroState.isActive && pomodoroState.startTime) {
      const now = Date.now();
      const totalPausedTime =
        pomodoroState.totalPausedTime +
        (pomodoroState.pausedAt ? now - pomodoroState.pausedAt : 0);

      const elapsed = Math.floor(
        (now - pomodoroState.startTime - totalPausedTime) / 1000
      );

      // Get original duration based on mode
      let originalDuration: number;
      switch (pomodoroState.mode) {
        case "work":
          originalDuration = 25 * 60; // 25 minutes
          break;
        case "shortBreak":
          originalDuration = 10 * 60; // 10 minutes
          break;
        case "longBreak":
          originalDuration = 15 * 60; // 15 minutes
          break;
        default:
          originalDuration = 25 * 60;
      }

      pomodoroState.timeLeft = Math.max(0, originalDuration - elapsed);

      // Check if timer finished
      if (pomodoroState.timeLeft <= 0) {
        await handleTimerComplete();
      }
    }

    return NextResponse.json({
      success: true,
      data: {
        ...pomodoroState,
        timeLeft: Math.max(0, Math.floor(pomodoroState.timeLeft)), // Ensure integer seconds
      },
    });
  } catch (error) {
    console.error("Error getting Pomodoro status:", error);
    return NextResponse.json(
      { success: false, error: "Failed to get Pomodoro status" },
      { status: 500 }
    );
  }
}

// POST - Start Pomodoro timer
export async function POST(request: NextRequest) {
  try {
    const body = await request.json();
    const { username, duration = 25 } = body;

    if (!username) {
      return NextResponse.json(
        { success: false, error: "Username is required" },
        { status: 400 }
      );
    }

    // Validate duration (1-120 minutes)
    if (duration < 1 || duration > 120) {
      return NextResponse.json(
        { success: false, error: "Duration must be between 1-120 minutes" },
        { status: 400 }
      );
    }

    // Stop existing timer
    if (pomodoroInterval) {
      clearInterval(pomodoroInterval);
    }

    // Set new timer state
    const durationInSeconds = duration * 60;
    pomodoroState = {
      timeLeft: durationInSeconds,
      isActive: true,
      mode: "work",
      session: pomodoroState.session,
      initiatedBy: username,
      nextBreakType: pomodoroState.session % 4 === 0 ? "long" : "short",
      startTime: Date.now(),
      pausedAt: null,
      totalPausedTime: 0,
    };

    // Start countdown interval (updates every second)
    startPomodoroInterval();

    // Emit update via Socket.io if available
    if (io) {
      io.emit("pomodoroUpdate", pomodoroState);
    }

    console.log(`ðŸ… Pomodoro started by ${username}: ${duration} minutes`);

    return NextResponse.json({
      success: true,
      data: pomodoroState,
      message: `Pomodoro started for ${duration} minutes`,
    });
  } catch (error) {
    console.error("Error starting Pomodoro:", error);
    return NextResponse.json(
      { success: false, error: "Failed to start Pomodoro" },
      { status: 500 }
    );
  }
}

// Start the countdown interval
function startPomodoroInterval() {
  if (pomodoroInterval) {
    clearInterval(pomodoroInterval);
  }

  pomodoroInterval = setInterval(() => {
    if (!pomodoroState.isActive || !pomodoroState.startTime) {
      return;
    }

    const now = Date.now();
    const elapsed = Math.floor(
      (now - pomodoroState.startTime - pomodoroState.totalPausedTime) / 1000
    );
    const originalDuration =
      pomodoroState.mode === "work"
        ? 25 * 60
        : pomodoroState.mode === "shortBreak"
        ? 10 * 60
        : 15 * 60;

    pomodoroState.timeLeft = Math.max(0, originalDuration - elapsed);

    // Emit real-time update
    if (io) {
      io.emit("pomodoroUpdate", {
        ...pomodoroState,
        timeLeft: pomodoroState.timeLeft,
      });
    }

    // Check if timer completed
    if (pomodoroState.timeLeft <= 0) {
      handleTimerComplete();
    }
  }, 1000); // Update every second for accurate display
}

// Handle timer completion
async function handleTimerComplete() {
  if (pomodoroInterval) {
    clearInterval(pomodoroInterval);
    pomodoroInterval = null;
  }

  const wasWorkSession = pomodoroState.mode === "work";

  if (wasWorkSession) {
    // Work session completed, start break
    const isLongBreak = pomodoroState.session % 4 === 0;
    const breakDuration = isLongBreak ? 15 * 60 : 10 * 60; // 15 or 10 minutes in seconds

    pomodoroState = {
      ...pomodoroState,
      timeLeft: breakDuration,
      mode: isLongBreak ? "longBreak" : "shortBreak",
      isActive: true, // Auto-start break
      startTime: Date.now(),
      pausedAt: null,
      totalPausedTime: 0,
    };

    console.log(
      `ðŸŽ‰ Work session ${pomodoroState.session} completed! Starting ${
        isLongBreak ? "long" : "short"
      } break.`
    );

    // Start break timer
    startPomodoroInterval();
  } else {
    // Break completed, stop timer (user decides when to start work)
    pomodoroState = {
      ...pomodoroState,
      timeLeft: 25 * 60, // Reset to 25 minutes
      mode: "work",
      isActive: false,
      session: pomodoroState.session + 1,
      startTime: null,
      pausedAt: null,
      totalPausedTime: 0,
      nextBreakType: (pomodoroState.session + 1) % 4 === 0 ? "long" : "short",
    };

    console.log(
      `âœ¨ Break completed! Ready for work session ${pomodoroState.session}.`
    );
  }

  // Emit completion update
  if (io) {
    io.emit("pomodoroUpdate", pomodoroState);
    io.emit("pomodoroComplete", {
      type: wasWorkSession ? "work" : "break",
      session: pomodoroState.session,
      nextMode: pomodoroState.mode,
    });
  }
}

// Export the pomodoro state and control functions for use by control API
export { pomodoroState, startPomodoroInterval, handleTimerComplete };
